<template>
  <div class="wishAdd">
    <van-nav-bar fixed title="添加心愿单" @click-left="$toPage(-1)">
      <van-icon name="arrow-left" size="24px" color="#666" slot="left" />
    </van-nav-bar>
    <van-cell-group class="groupO">
      <van-field
        v-model="DrugsName"
        required
        clearable
        label-width="120"
        label="药品名称"
        maxlength="15"
        placeholder="请输入药品名称"
      />
      <van-field
        v-model="Specifications"
        label-width="120"
        label="规格"
        maxlength="15"
        placeholder="请输入规格"
        required
      />
      <van-field
        v-model="Manufactor"
        label-width="120"
        label="生产厂家"
        maxlength="40"
        placeholder="请输入生产厂家"
        required
      />
    </van-cell-group>
    <van-cell-group class="groupT">
      <van-field
        v-model="num"
        clearable
        label-width="120"
        type="number"
        label="采购数量"
        maxlength="6"
        placeholder="请输入采购数量"
      />
      <van-field
        v-model="price"
        label-width="120"
        type="number"
        label="药品价格"
        maxlength="8"
        @input="input"
        placeholder="请输入药品价格"
      />
      <van-field
        v-model="describe"
        label-width="120"
        type="textarea"
        maxlength="50"
        label="其他描述(选填)"
        autosize
        placeholder="可填写详细描述，没有可不填"
        >
        <p slot="button" style="font-size:12px">{{ describe.length + '/50' }}</p>
      </van-field>
      <van-cell title="添加相关图片(选填)" value="只可上传一张图片" />
      <div class="upload" @click="upload">
        +
        <img v-if="url" :src="url" />
      </div>
    </van-cell-group>
    <van-button type="danger" @click="commit">提交</van-button>
    <div v-show="overlay" class="overlay">
      <van-loading type="spinner" color="#fff" size="65px" />
    </div>
  </div>
</template>
<script>
import md5 from "md5";
export default {
  data() {
    return {
      overlay: false,
      DrugsName: "",
      Specifications: "",
      Manufactor: "",
      num: "",
      price: "",
      describe: "",
      url: "",
      list: []
    };
  },
  methods: {
    upload() {
      JNB._requestNative("openImageSelect", url => {
        if (url) {
          this.url = url;
        }
      });
    },
    commit() {
      this.overlay = true
      JNB.query({
        serverName: "goodsServer",
        cls: "WishListModule",
        method: "add",
        params: {
          prodname: this.DrugsName,
          spec: this.Specifications,
          manuname: this.Manufactor,
          num: this.num,
          price: this.price,
          detail: this.describe
        },
        callback: data => {
          let result = JSON.parse(data);
          if(result.code == 200){
            this.resetImg()
          }else{
            this.$toast.fail(result.message)
            this.overlay = false
          }
        }
      });
    },
    resetImg() {
      JNB.query({
        serverName: "globalServer",
        cls: "FileInfoModule",
        method: "fileServerInfo",
        params: {
          compid: this.$loadUserInfo().comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            const list = [];
            const o = result.data;
            list.push({
              remotePath: o.wishListPath,
              fileName: md5(this.DrugsName + this.Specifications + this.Manufactor) + ".jpg",
              uri: this.url
            });
            JNB._requestNative(
              "fileUpload",
              JSON.stringify({ url: o.upUrl, files: list }),
              json => {
                const data = JSON.parse(json);
                if (data.code === 200) {
                  this.$toast('提交成功！')
                  this.overlay = false
                } else {
                  this.$toast.fail("图片上传失败！");
                  this.overlay = false
                }
              }
            );
          }else{
            this.$toast.fail("图片上传失败！");
            this.overlay = false
          }
        }
      });
    },
    getWishList() {
      JNB.query({
        serverName: "goodsServer",
        cls: "WishListModule",
        method: "queryWishList",
        params: {
          pageIndex: 1,
          pageNumber: 100
        },
        callback: data => {
          let result = JSON.parse(data);
          if(result.code == 200){
            this.list = result.data
          }
        }
      });
    },
    input() {
      this.price = this.price.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
    }
  }
};
</script>
<style scoped>
.wishAdd {
  padding-top: 82px;
  background-color: #f5f5f5;
}
.wishAdd .groupO {
  margin: 20px 0;
  padding: 0 20px;
}
.wishAdd .groupT {
  padding: 0 20px 20px;
}
.wishAdd .groupT .upload {
  width: 200px;
  height: 200px;
  border: 1px dashed #ccc;
  margin: 10px 40px;
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  line-height: 200px;
  color: #f5f5f5;
  position: relative;
}
.wishAdd .groupT .upload img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}
.wishAdd .van-button {
  width: 710px;
  border-radius: 8px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}
.wishAdd .overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.4;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
